body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.menu_section{
  padding-top: 100px;
  margin: 0 auto;
}

.title{
  text-align: center;
}

.title h2{
  margin-bottom: 10px;
}

.underline{
  width: 60px;
  height: 3px;
  background: #c59d5f;
  margin-left: auto;
  margin-right: auto;
}

.section__center{
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 5rem 7rem;
  margin: 0 auto;
  max-width: 1170px;
  width: 90vw;

}

.menu__item{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem 2rem;
  max-width: 40rem;
}

.photo{
  object-fit: cover;
  height: 150px;
  width: 100%;
  border: 0.25rem solid #c59d5f;
  display: block;
  border-radius: 3px;
}

.item__info{
 padding: 0;
}

.item__info header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 0.5px dotted hsl(210, 22%, 49%);
  padding: 0px;
} 

.menu__item__title{
  margin-top: 0;
}

.price{
  margin-top: 0;
}

.btn__container{
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 20px;
  margin-bottom: 60px;
  
}

.filter__btn{
  background: transparent;
  border-color: transparent;
  font-size: 16px;
  padding: 10px;
  color: #c59d5f;
  transition: 0.2s;
  text-transform: capitalize;
  letter-spacing: 1px;
  cursor: pointer;
  border-radius: 7px;
}

.filter__btn:hover {
  background: #c59d5f;
  color: white;
}